@import '../../globalVariables.scss';

.catalog_wrapper {
   position: relative;
   width: 100%;
   height: 100%;
   padding: 50px;
   left: 50%;
   transform: translateX(-50%);

    .welcome {
        text-align: center;
        color: $main-light;
        font-size: 3rem;
        font-weight: 700;

        @media (max-width: 860px) {
            font-size: 2rem;
         }  

         @media (max-width: 520px) {
            font-size: 1.5rem;
         }  
         
         @media (max-width: 390px) {
            font-size: 1.2rem;
         }  
    }
}